import React, { useState, useEffect } from 'react';
import { usePWAStatus } from 'Context';
import { addMisc } from 'dataAccess';

// note for chrome for testing: chrome://flags/#bypass-app-banner-engagement-checks

function PWAButton() {
  const [deferredPrompt, setDeferredPrompt] = useState(null);
  const { isInstalled, isPWA, isSupported, isChecking } = usePWAStatus();

  useEffect(() => {
    const handleBeforeInstallPrompt = (e) => {
      e.preventDefault();
      setDeferredPrompt(e);
    };

    window.addEventListener('beforeinstallprompt', handleBeforeInstallPrompt);

    return () => {
      window.removeEventListener(
        'beforeinstallprompt',
        handleBeforeInstallPrompt
      );
    };
  }, []);

  const handleInstallClick = () => {
    if (deferredPrompt) {
      deferredPrompt.prompt();
      deferredPrompt.userChoice.then((choiceResult) => {
        if (choiceResult.outcome === 'accepted') {
          console.log('User accepted the PWA installation');
          addMisc("installed", true);
        } else {
          console.log('User dismissed the PWA installation');
        }
        setDeferredPrompt(null);
      });
    } else {
      alert(
        "To install the app, please use your browser's menu to add it to your home screen."
      );
    }
  };

  // const handleOpenPWA = () => {
  //   const hasBaseURI = window.location.href.includes('/pwa-test');
  //   window.location.href = hasBaseURI ? '/pwa-test/' : '/';
  // };

  if (isChecking) {
    return null;
  }

  if (isInstalled) {
    return null;
    // return <div>Already installed.</div>;
  }

  if (isPWA) {
    return null;
  }

  if (!isSupported && !deferredPrompt) {
    return <p>Unable to install WEG PWA.</p>;
  }

  // This doesn't work
  // const openPwaButton = (
  //   <button
  //     onClick={handleOpenPWA}
  //     style={{
  //       border: 'none',
  //       backgroundColor: '#e6ba08',
  //       padding: '10px 20px',
  //       borderRadius: '8px',
  //       cursor: 'pointer',
  //       display: 'inline-flex',
  //       alignItems: 'center',
  //       boxShadow: '0 4px 6px rgba(0, 0, 0, 0.1)',
  //       backdropFilter: 'blur(5px)',
  //       transition: 'transform 0.1s ease-in-out',
  //       color: '#000',
  //       fontWeight: '600',
  //       fontSize: '14px',
  //     }}
  //     onMouseDown={(e) => (e.currentTarget.style.transform = 'scale(0.95)')}
  //     onMouseUp={(e) => (e.currentTarget.style.transform = 'scale(1)')}
  //     onMouseLeave={(e) => (e.currentTarget.style.transform = 'scale(1)')}
  //   >
  //     <img
  //       src="favicon.png"
  //       alt="TRADOC Logo"
  //       style={{ width: '24px', height: '24px', marginRight: '8px' }}
  //     />
  //     <img
  //       src="Progressive_Web_Apps_Logo.svg"
  //       alt="Open PWA"
  //       style={{ width: '32px', height: '32px', marginRight: '8px' }}
  //     />
  //     <span>Open PWA</span>
  //   </button>
  // );

  const installPwaButton = (
    <button
      onClick={handleInstallClick}
      style={{
        border: 'none',
        backgroundColor: '#e6ba08',
        padding: '10px',
        borderRadius: '8px',
        cursor: 'pointer',
        display: 'inline-flex',
        alignItems: 'center',
        boxShadow: '0 4px 6px rgba(0, 0, 0, 0.1)',
        backdropFilter: 'blur(5px)',
        transition: 'transform 0.1s ease-in-out',
      }}
      onMouseDown={(e) => (e.currentTarget.style.transform = 'scale(0.95)')}
      onMouseUp={(e) => (e.currentTarget.style.transform = 'scale(1)')}
      onMouseLeave={(e) => (e.currentTarget.style.transform = 'scale(1)')}
    >
      <img
        src="favicon.png"
        alt="TRADOC Logo"
        style={{ width: '24px', height: '24px', marginRight: '8px' }}
      />
      {/* <img
        src="Progressive_Web_Apps_Logo.svg"
        alt="Install App"
        style={{ width: '32px', height: '32px', marginRight: '8px' }}
      /> */}
      <span style={{ fontWeight: '600', fontSize: '14px', color: '#000' }}>
        Install App
      </span>
    </button>
  );

  return isInstalled ? null : installPwaButton;
  // if (isInstalled) {
  //   return openPwaButton;
  // } else {
  //   return installPwaButton;
  // }
}

export default PWAButton;
