import React, { useState } from 'react';
import Modal from 'UI/Modal';
import PWAButton from 'Components/PWAButton';
import './InstallModal.css';

export default function InstallModal({ close, isFullScreen }) {
  const [currentTab, setCurrentTab] = useState("About");
  const [currentStepIndex, setCurrentStepIndex] = useState(0);

  const instructionsData = {
    About: [
      {
        image: 'howto/about_0.webp',
        caption: 'ODIN WEG can now be installed on both your desktop and your mobile device. Refer to the instructions in the "Desktop" and "Mobile" tabs for more information.',
      },
      {
        caption: 'Installing WEG Application allows users to import the WEG onto their device, take it offline, and continue to use the WEG.',
      },
      {
        caption: 'Installation is not necessary for offline functionality, but installing WEG Application enables persistant storage for the app, and allows you to use the WEG like any other app on your device.'
      },
      {
        image: 'howto/import_1.webp',
        caption: 'The new Import Manager allows users to download and save the ODIN WEG. Once the WEG has been imported, users may take their device to a location without internet access. There, they may open WEG Application and refer to the WEG using the data they imported.'
      },
      {
        caption: 'Data can later be re-imported when returning online to obtain the latest WEG Asset information and images.'
      },
      {
        caption: 'Refer to the Import tab for more information on using the Import Manager.'
      }
    ],
    Desktop: [
      {
        alt: 'ODIN App Install Button',
        caption:
          'If the "Install App" button above this text is present, click on it after having read steps 3-7.',
          component: <div className="pwa-button"><PWAButton /></div>,
      },
      {
        image: 'howto/pwa_install_desktop_2.webp',
        alt: 'ODIN App install via browser bar',
        caption:
          'If the button was not present, or if you received a message about using your browser\'s menu, click the "Install WEG Application" icon in the browser search bar.',
      },
      {
        image: 'howto/pwa_install_desktop_3.webp',
        alt: 'ODIN App install configure permissions',
        caption: 'Follow the prompts to complete the installation.',
      },
      {
        image: 'howto/pwa_install_desktop_4.webp',
        alt: 'ODIN App install permissions',
        caption:
          'WEG Application will be added to your desktop. Configure the desired permissions within the Windows prompt for the application.',
      },
      {
        image: 'howto/pwa_install_desktop_5.webp',
        alt: 'ODIN App install pin to taskbar',
        caption:
          'Confirm or deny the Windows prompt for adding WEG Application to your taskbar.',
      },
      {
        image: 'howto/pwa_install_desktop_6.webp',
        alt: 'ODIN App installed',
        caption:
          'WEG Application has been installed. It can be found on your desktop by pressing the "Windows" key and searching for "WEG Application."',
      },
      {
        image: 'howto/pwa_install_desktop_7.webp',
        alt: 'ODIN App uninstall',
        caption:
          'To uninstall WEG Application, right-click on the app icon in the Windows Start Menu and select "Uninstall." Refer to the previous step on how to find the app on your desktop.',
      },
      {
        image: 'howto/pwa_install_desktop_8.webp',
        alt: 'ODIN App uninstall',
        caption:
          'Depending on the browser you used to install WEG Application, you might be directed to "Installed Apps" in your Windows Settings. To continue uninstalling WEG Application, click on the three horizontal dots, select "Uninstall" and proceed with the next prompt.',
      },
      {
        image: 'howto/pwa_install_desktop_9.webp',
        alt: 'ODIN App uninstall',
        caption: 'Checking "Also delete data from Chrome" will also remove all saved data associated with the website. This will include all imported data, and the cache for the website.',
      },
    ],
    Mobile: [
      {
        alt: 'ODIN App Install Button',
        caption:
          'If the "Install App" button above this text is present, tap on it after having read steps 5-7.',
        component: <div className="pwa-button"><PWAButton /></div>,
      },
      {
        image: 'howto/pwa_install_mobile_1.webp',
        alt: 'Chrome Menu',
        caption:
          'If the button was not present, tap on the three vertical dots on the top right of your browser\'s screen.',
      },
      {
        image: 'howto/pwa_install_mobile_2.webp',
        alt: 'Add to Home Screen',
        caption: 'Tap "Add to Home Screen".',
      },
      {
        image: 'howto/pwa_install_mobile_3.webp',
        alt: 'Install Prompt',
        caption: 'Tap "Install".',
      },
      {
        image: 'howto/pwa_install_mobile_4.webp',
        alt: 'Installation Confirmation',
        caption: 'Tap "Install" again.',
      },
      {
        image: 'howto/pwa_install_mobile_5.webp',
        alt: 'Find App',
        caption:
          'Find "ODIN" in your installed apps. Optionally, add it to your home screen.',
      },
      {
        image: 'howto/pwa_install_mobile_6.webp',
        alt: 'Open App',
        caption: 'Open the app and proceed to use it normally.',
      },
    ],
    Importing: [
      {
        image: 'howto/import_0.webp',
        caption: 'Press the "Import Manager" button on the landing page to open the Import Manager. The landing page can be accessed at any time by pressing the "Home" link at the bottom of the page.'
      },
      {
        image: 'howto/import_1.webp',
        caption: 'The "Enable Storage" button requests for the page to be granted persistant storage. Not all browsers support this feature. For the best experience, use WEG Application after installing it from Chrome. If persistant storage has been granted, the button will be disabled, and it will instead read "Storage Enabled."'
      },
      {
        image: 'howto/import_4.webp',
        caption: 'The "Import Assets" button retrieves the latest WEG Asset information from the ODIN database. The information imported during this process includes all WEG Asset information excluding their images. This information will be stored locally on your device.'
      },
      {
        image: 'howto/import_2.webp',
        caption: 'The "Import Images" button retrieves all of the images associated with the WEG Assets downloaded when running "Import Assets." Imported images are saved locally in a highly compressed format to save storage space. By default, it will only include the first image for each WEG Asset. Images that have not been downloaded will instead render a placeholder image.'
      },
      {
        image: 'howto/import_3.webp',
        caption: 'The "Limit" field next to the "Import Images" button configures the maximum number of images downloaded for each WEG Asset. Increasing this number raises the maximum number of images downloaded for each WEG Asset. WEG Assets typically contain three images.'
      },
      {
        caption: 'Note that downloading images may require anywhere from half an hour to several hours to complete, depending on the speed of your internet connection and the limit you have configured.'
      },
      {
        caption: 'If WEG Application is not active and open on your device, the device will either cancel or suspend the ongoing import until it is opened again. Imports will proceed even if the Import Manager is closed while the app is open. Reopening the Import Manager will display an ongoing import.'
      },
      {
        image: 'howto/import_5.webp',
        caption: 'If the download process is interrupted or canceled, it may later be resumed by starting the process again. Images that have already been downloaded will be skipped. Note that in this case, the download timer will not give an accurate estimate.'
      },
      {
        caption: 'The "Clear Storage" button removes all WEG Asset information and images that have been saved to your device\'s storage, including some user configuration information used by WEG Application. It must be held for several seconds in order to prevent accidental deletion.'
      },
      {
        caption: 'The text log displays information about ongoing imports. Warnings are displayed in yellow text. Errors are displayed in red text. The "Filter Level" button toggles from diplaying all messages to just warnings and errors. The presence of errors is expected when images hosted within the ODIN database are missing required information for the import. WEG Application will continue to function if some images could not be imported.'
      },
      {
        caption: 'Information about your device\'s used and available storage is displayed below the text log. Note that the "Storage Available" shows your browser\'s estimate of your available storage, and it is not always accurate.'
      }
    ]
  };

  const currentInstructions = instructionsData[currentTab];
  const totalSteps = currentInstructions.length;

  const handleNext = () => {
    setCurrentStepIndex((prev) => (prev < totalSteps - 1 ? prev + 1 : prev));
  };

  const handlePrev = () => {
    setCurrentStepIndex((prev) => (prev > 0 ? prev - 1 : prev));
  };

  const handleTabChange = (tab) => {
    setCurrentTab(tab);
    setCurrentStepIndex(0); // Reset to first step on tab change
  };

  return (
    <Modal
      showShare={false}
      open={() => ({
        section: 'WEG',
        title: 'How to Install WEG Application',
        children: (
          <div className="install-modal-wrapper">
            <div className="install-modal-tabs">
              {Object.keys(instructionsData).map((tab) => (
                <button
                  key={tab}
                  className={`tab-button ${currentTab === tab ? 'active' : ''}`}
                  onClick={() => handleTabChange(tab)}
                >
                  {tab}
                </button>
              ))}
            </div>

            <div className="carousel-container">
              <div className="carousel-content">
                {currentInstructions[currentStepIndex].component ? (
                  currentInstructions[currentStepIndex].component
                ) : (
                  <img
                    src={currentInstructions[currentStepIndex].image}
                    alt={currentInstructions[currentStepIndex].alt}
                    className="carousel-image"
                  />
                )}

                <div className="carousel-caption">
                  {currentInstructions[currentStepIndex].caption}
                </div>
              </div>

              <div className="carousel-navigation">
                <button
                  className="nav-button"
                  onClick={handlePrev}
                  disabled={currentStepIndex === 0}
                >
                  Previous
                </button>
                <span className="step-indicator">
                  Step {currentStepIndex + 1} of {totalSteps}
                </span>
                <button
                  className="nav-button"
                  onClick={handleNext}
                  disabled={currentStepIndex === totalSteps - 1}
                >
                  Next
                </button>
              </div>
            </div>
          </div>
        ),
        modalStyle: isFullScreen
          ? { width: '100%', height: '100%' }
          : { width: '83%', height: '83%' },
      })}
      autoOpen
      close={close}
      identifier="Install Modal"
    />
  );
}
